import React, { useState, useEffect } from "react"
import { ethers } from "ethers";
import contractABI from "../contracts/Renkon.json";
import Layout from '../layouts/main'
import { Button, Box, TextInput } from "grommet"
const contractAddress = '0x7208DA4d2Fbc7C3598b65Faf0df58F8a00B7B0F2';

const getWindowEth = () => window !== undefined && window.ethereum;

const getWhitelistProof = (address, type) => {
  return fetch(`/whitelist/${type}/${address}.json`)
  .then(e => e.json())
  .then(e => {
    return e.proof;
  })
}

const requestConnectWallet = async () => {
  const eth = getWindowEth()
  if(eth) {
    try {
      const provider = new ethers.providers.Web3Provider(eth)
      await provider.send("eth_requestAccounts", [])
      return await provider.getSigner()
    }
    catch(e) {
      console.log(e)
    }
  }
}

const marketingMint = async (contract: any, proof: any, quantity: number, price = 25000000) => {
  const isWL = await contract.marketingMint(proof, quantity);
  console.log(isWL)
}

const callContract = async (contract: any, method: any, param: any) => {
  return await contract[method](param)
}



// markup
const IndexPage = () => {

  const [contract, setContract] = useState(null);
  const [walletAddress, setWalletAddress] = useState(null)
  const [proof, setProof] = useState(null)
  const [mintQuantity, setMintQuantity] = useState(1)
  const [myNFT, setMyNFT] = useState([])
  const [nftMeta, setNftMeta] = useState([])

  const getMyNFT = async(address) => {
    return await callContract(contract, 'tokensOf', address)
  }

  const getMetaFile = async (tokenId) => {
    const url = await callContract(contract, 'tokenURI', tokenId)
    return fetch(url)
    .then(e => e.json());
  }

  const initializeContract = async () => {
    const signer = await requestConnectWallet();
    if(signer) {
      signer.getAddress().then(address => {
        setWalletAddress(address)
      });

      const contractObject = new ethers.Contract(contractAddress, contractABI, signer) as Renkon;
      setContract(contractObject)
    }
  }

  useEffect(() => {
    if(walletAddress) {
      getWhitelistProof(walletAddress, 'marketing')
      .then(e => {
        setProof(e)
      })
      
      getMyNFT(walletAddress)
      .then(e => {
        console.log(e)  
        setMyNFT(e)
      }
      )
    }

  }, [walletAddress])

  useEffect(() => {
    Promise.all(myNFT.map((nftTokenId) => {
      return getMetaFile(nftTokenId)
    })).then((allMeta: any) => {
      setNftMeta(allMeta)
    })
  }, [myNFT])
    

  return (
    <Layout>
 
      <Box>

      </Box>
      <Box gap="small">
      <h6>contract: {contractAddress}</h6>
      {
          walletAddress && 
          <>
            <Button color="accent-1" primary label={`Login as ${walletAddress}`}/>
          </>
          
        }
        {
          !walletAddress && <Button label="Connect Wallet" onClick={initializeContract} primary></Button>
        } 
      <TextInput
      placeholder="Quantity"
      value={mintQuantity}
      onChange={event => setMintQuantity(event.target.value)}
      />
      {
        contract && proof && <>
          <Button label="Mint" onClick={() => {
            marketingMint(contract, proof, mintQuantity)
          }} primary></Button>
        </>
      }

      {
        nftMeta.map(e => <>{ e.name }<img src={e.image} /></>)
      }
      </Box>
      


    </Layout>
  )
}

export default IndexPage
